.button {
    border-radius: 3px;
    border: none;
    color: #002E5C;
    padding: 10px 50px;
    font-weight: bold;
}

.disabled {

}

.secondary {
    background-color: whitesmoke;
    color: #0066CC;
    border: 1px solid #0066CC;
}

.default {
    background-color: #0066CC;
    color: white;
}

.red {
    background-color: #CC1439;
    color: white;
}

.default:hover {
    background-color: #0080FF;
    color: white;
}

.red:hover {
    background-color: #FF1A47;
    color: white;
}

.secondary:hover {
    background-color: #E5F0FA;
    color: #0066CC;
    border-color: #0066CC;
}


.default:active {
    background-color: #0059B2;
    color: white;
}

.secondary:active {
    background-color: #C9E5FF;
    color: #0066CC;
    border-color: #0066CC;
}

.red:active {
    background-color: #B21232;
    color: white;

}

.button:disabled {
    background-color: palevioletred;
    color: lightgrey;
    cursor: no-drop;
}
