
.sidebar {
    position: fixed;
    visibility: hidden;
    display: flex;
    flex-direction: column;
    width: 30%;
    box-shadow: 0px 10px 40px rgba(29, 33, 38, 0.13), 0px 1px 2px rgba(29, 33, 38, 0.1);
    left: -100%;
    z-index: 2;
    height: 100%;
    background-color: white;
    transition: 1s;

}

.sidebar.open {
    left: 0;
    visibility: visible;
    position: fixed;

}

.background {
    position: fixed;
    z-index: 1;
    width: 1000vw;
    height: 100vh;
    background: #000000;
    opacity: 0.3;
}

.nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.nav a {
    text-decoration: none;
    padding: 20px;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    color : black;
}

.nav a:hover {
    color : #58ACFF;
}

.nav a.active {
    color : #0059B2;
    text-decoration-line: underline;
}
.close {
    position: relative;
}

.close img {
    position: absolute;
    right: 5px;
}


























